@import url(https://fonts.googleapis.com/css2?family=Raleway&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  font-family: 'Raleway', sans-serif!important;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.featurePd {
    padding: 3rem 0;
}

.featurePd h2 {
    font-size: 3rem;
    font-weight: 100;
    color: #fff;
    padding: 2.5rem 0;
}

.productCard {
    background: #212326;
}

.productCard h5 {
    color: #fff;
    margin: 0.4rem 0 0.2rem;
    font-weight: 200;
}

.productCard img{
    width: 100%;
}

.productCardDes {
    padding: 1rem;
    color: #a4a7a9;
    margin-bottom: 3rem;
}

.animateBtn {
    color: #fff;
    border: 2px solid #58adb7;
    background: none;
    padding: 0.5rem 1rem;
    font-size: 0.9rem;
    font-weight: 500;
    cursor: pointer;
    z-index: 2;
    transition: 0.8s;
    position: relative;
    overflow: hidden;
    text-transform: uppercase;
}

.animateBtn::before{
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 0%;
    background: #58adb7;
    z-index: -1;
    transition: 0.4s;
}

.animateBtn:hover::before{
    height: 100%;
}

.viewBtn {
    color: #fff;
    border: none;
    background: #58adb7;
    padding: 0.6rem 1rem;
    font-size: 1rem;
    font-weight: 500;
    cursor: pointer;
    z-index: 2;
    transition: 0.8s;
    position: relative;
    overflow: hidden;
    text-transform: uppercase;
}

.viewBtn::before{
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 0%;
    background: #3a6f75d0;
    z-index: -1;
    transition: 0.4s;
}

.viewBtn:hover::before{
    height: 100%;
}

.productPrice {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

@media (max-width: 768px) {
    .productPrice {
        display: block;
    }
    .productPrice > span {
        display: block;
        margin-bottom: 0.5rem;
    }
    .animateBtn {
        padding: 0.4rem 0.8rem;
        font-weight: 400;
        font-size: 0.9rem;
    }
}
#navBar {
    z-index: 88;
    background: #282B2E;
    font-family: sans-serif;
}

#navBar img {
    height: 2.7rem;
}

#navBar a {
    color: #fff;
    font-weight: 400;
    font-size: 1.1rem;
    text-decoration: none;
    margin: 0 1rem;
    transition: 0.4s;
}

#navBar a:hover {
    color: #58ADB7;
}



@media (max-width: 768px) {
    #navBar .nav-item{
        text-align: center!important;
        margin: 0.5rem 0;
    }
    .navbar-toggler {
        border-color: #333638!important;
    }
}

.header {
    height: 100vh;
    overflow: hidden;
    background-image: url(/sagazirobiul/zopnik-restaurant/static/media/header-bg.52fdde60.jpg);
    background-size: 75%;
    background-position: right;
    background-repeat: no-repeat;
    position: relative;
}

.header::after {
    position: absolute;
    content: ' ';
    top: 0;
    left: 0;
    width: 25%;
    height: 100%;
    z-index: 0;
    background: #212326;
}

.headerContent {
    z-index: 11;
}

.header h1 {
    color: #fff;
    font-size: 4rem;
    font-weight: 100;
}

.header h5 {
    color: #a4a7a9;
    font-size: 1.5rem;
    font-weight: 200;
    padding: 1rem 0;
}

.headerRow {
    height: 100vh;
}

.btnLg {
    padding: 1.5rem 2.5rem;
    font-size: 1rem;
}

@media (max-width: 768px){
    .header {
        background-size: cover;
    }
    .header h1 {
        font-size: 3rem;
    }
    .btnLg {
        padding: 1rem 2rem;
    }
}
.promo {
    background-image: linear-gradient(rgba(0, 0, 0, 0.5),rgba(0,0,0,0.5)), url(/sagazirobiul/zopnik-restaurant/static/media/v-bg.ac79bf0d.jpg);
    background-repeat: no-repeat;
    background-size: 100%;
    padding: 6rem 0;
}

.promo h1 {
    color: #fff;
    font-size: 4.8rem;
    line-height: 1.2;
    font-weight: 100;
}

.promo p {
    color: rgba(255, 255, 255, 0.502);
    font-size: 1.5rem;
    font-weight: 100;
}

.playBtn {
    color: #fff;
    background: transparent;
    outline: none;
    border: none;
    height: 6rem;
    width: 6rem;
    border-radius: 50%;
    border: 1px solid #fff;
    font-size: 1rem;
    transition: 0.3s;
    margin-bottom: 1rem;
}

.playBtn:hover{
    background: #fff;
    color: #212326;
}

@media (max-width: 768px) {
    .promo {
        background-size: cover;
    }
    .promo h1 {
        font-size: 2.4rem;
    }
    .flex-item:nth-of-type(1) { -webkit-order: 2; order: 2; }
    .flex-item:nth-of-type(2) { -webkit-order: 1; order: 1; }
}
.review {
    background: url(/sagazirobiul/zopnik-restaurant/static/media/r-bg.6b75c5e2.png);
    background-repeat: no-repeat;
    background-position: right;
    background-size: contain;
    width: 100%;
    padding: 3rem 0 6rem;
}

.stars {
    padding: 1.5rem 0;
}

.stars span {
    color: #EBD072;
    margin: 0 0.2rem;
    font-size: 1rem;
}

.review h1 {
    color: #fff;
    font-size: 3.5rem;
    font-weight: 100;
} 

.review p {
    color: #a4a7a9;
    padding: 2rem 0;
}

.userInfo {
    padding: 0.5rem 1rem;
}

.userInfo img {
    height: 35px;
    width: 35px;
    border-radius: 50%;
}

.userStars span {
    color: #EBD072;
    margin: 0 0.277rem;
    font-size: 0.8rem;
}

.googleReview,
.linkedInReview {
    padding: 2rem;
    margin-bottom: 0.3rem;
}

.review h4 {
    font-size: 1.4rem;
    font-weight: 200;
    color: #fff;
    padding: 1rem 0;
}

.googleReview {
    background: #333638;
    position: relative;
}

.googleReview::after {
    top: 100%;
    right: 0;
    width: 0;
    height: 0;
    position: absolute;
    border-color: rgba(0,0,0,0) #333638 rgba(0,0,0,0) rgba(0,0,0,0);
    border-style: solid;
    border-width: 0 1.4rem 1.4rem 0;
    content: " ";
}

.review1 {
    margin-right: 5rem;
}

.linkedInReview {
    background: #212326;
    position: relative;
    margin-top: 0.5rem;
}

.linkedInReview::after {
    top: 100%;
    right: 0;
    width: 0;
    height: 0;
    position: absolute;
    border-color: rgba(0,0,0,0) #212326 rgba(0,0,0,0) rgba(0,0,0,0);
    border-style: solid;
    border-width: 0 1.4rem 1.4rem 0;
    content: " ";
}

.review2 {
    margin-left: 5rem;
}

.darkColor {
    color: #a4a7a9;
}

@media (max-width: 768px){
    .review {
        background-size: cover;
    }
    .review h1 {
        font-size: 2.3rem;
    }
    .review p {
        padding: 1rem 0;
    }
    .googleReview,
    .linkedInReview {
    padding: 1rem;
    margin-bottom: 0.3rem;
    }
}

.footer {
    background: #282B2E;
    padding: 3rem 0;
}

.footer .fLogo {
    height: 3.7rem;
}

.footer h5 {
    color: rgba(255, 255, 255, 0.5);
    font-weight: 300;
    font-size: 1.4rem;
    padding: 0.8rem 0;
}

.inputGroup {
    padding: 0.8rem 0;
}

.inputGroup input {
    width: 70%;
}
.inputGroup input:focus {
    border: none;
    outline: none;
}
.inputGroup button {
    width: 30%;
}
.inputGroup input,
.inputGroup button {
    border: none;
    padding: 0.8rem 1rem;
}


.socialIcons {
    margin: 0;
    padding: 0;
}
.socialIcons li {
    list-style: none;
    float: left;
}
.socialIcons li a {
    display: block;
    width: 2rem;
    height: 2rem;
    border-radius: 50%;
    text-align: center;
    line-height: 2rem;
    background: #3e4043;
    margin-right: 5px;
    color:#fff;
    transition: 0.4s;
}

.facebook:hover{
    background-color: #4381F3;
}
.google:hover{
    background-color: #D04F3E;
}
.twitter:hover{
    background-color: #33b4f0;
}
.youtube:hover{
    background-color: #f73a24;
}
.instagram:hover{
    background-color: #E51CA5;
}

.footerBottom {
    border-top: 1px solid #3e4043;
}

.footerBottom span {
    color: #fff;
}

.footerBottom img {
    height: 13rem;
}

@media (max-width: 768px) {
    .inputGroup input,
    .inputGroup button {
        border: none;
        padding: 0.5rem 0.8rem;
    }
}
.slideImg {
    position: relative;
    cursor: pointer;
    overflow: hidden;
}

.slideImg img {
    width: 100%;
    transition: 0.9s;
}

.slideImg:hover img {
    -webkit-transform: scale(1.06);
            transform: scale(1.06);
}

.slideImg h4 {
    position: absolute;
    bottom: 4rem;
    right: 4rem;
    color: #fff;
    font-size: 3.5rem;
    font-weight: 200;
}

.swiper-slide,
.swiper-slide-next {
    margin: 0!important;
}
.steps {
    background: #212326;
    padding: 5rem 0;
}

.stepDetails {
    padding: 0.5rem;
}

.stepDetails h4{
    color: #fff;
    font-weight: 300;
    font-size: 1.4rem;
}

.stepDetails p {
    color: #a4a7a9;
    font-weight: 400;
    font-size: 0.98rem;
}

.stepIcon {
    color: #58ADB7;
    font-size: 2.5rem;
    margin-right: 1rem;
}
.gridBanner {
    height: 200px;
    overflow: hidden;
}

.gridBox {
    border: 1px solid #e0e0e0;
    padding: 1rem;
}

.gridBanner img {
    width: 100%;
}

.gridDetails img{
    width: 100%;
}

.gridDetails h6 {
    font-size: 1.1rem;
    font-weight: 500;
}
